        'Argentina': 'Argentina',
        'Iceland': 'Islndia',
        'Nigeria': 'Nigria',
        'Brazil': 'Brasil',
        'Switzerland': 'Sua',
        'Costa Rica': 'Costa Rica',
        'Serbia': 'Srvia',
        'Germany': 'Alemanha',
        'Mexico': 'Mxico',
        'Sweden': 'Sucia',
        'South Korea': 'Coria do Sul',
        'Belgium': 'Blgica',
